.body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  font-family: "Nunito", "Roboto", "Arial", sans-serif;
  background-color: #F8F0FB;
  transition: 0.5s;
}

*::-webkit-scrollbar {
  width: 2px;
  height: 2px;
}

*::-webkit-scrollbar-track {
  background-color: #F8F0FB;
}

*::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #6320EE;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.blur .background__overlay {
  backdrop-filter: blur(1px);
}
.blur .settings {
  backdrop-filter: blur(1px);
}
.blur .statistics {
  backdrop-filter: blur(1px);
}
.blur .control {
  backdrop-filter: blur(1px);
}
.blur .console {
  backdrop-filter: blur(1px);
}

@font-face {
  font-display: swap;
  font-family: "Nunito";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/nunito-300.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Nunito";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/nunito-400.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Nunito";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/nunito-700.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Chalet";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/chalet-300.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Chalet";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/chalet-500.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Pricedown";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/pricedown.woff2") format("woff2");
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  padding: 25px;
  transition: 0.5s;
  z-index: 1;
}
.header--hide {
  transform: translateY(-100%);
}

.header__logo {
  width: 150px;
  height: 45px;
  margin-right: 15px;
  transition: 0.3s;
  transition-delay: 0.2s;
}
.header__logo path {
  transition: 0.3s;
  transition-delay: 0.2s;
}

.header__ufh {
  width: 35px;
  height: 23px;
  margin-right: auto;
  border-radius: 4px;
  transition: 0.2s;
}
.header__ufh--off {
  transform: scale(0);
}

.header__menu {
  width: 45px;
  height: 45px;
  transition: 0.2s;
}
.header__menu--error {
  animation-name: shake;
  animation-duration: 0.08s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.header__menu--error .header__icon {
  fill: #FF3434;
}

.header__icon {
  transition: 0.3s;
}

@keyframes shake {
  0% {
    transform: translateX(-5%);
  }
  100% {
    transform: translateX(5%);
  }
}
.header__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 28px;
  height: 28px;
  transform: translate(-50%, -50%);
}

@media (min-width: 768px) {
  .header__logo {
    width: 180px;
    margin-right: 25px;
  }
  .header__ufh {
    width: 45px;
    height: 30px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding: 35px;
  }
  .header__logo {
    width: 250px;
    margin-right: 35px;
  }
  .header__ufh {
    width: 60px;
    height: 40px;
  }
  .header__menu {
    width: 60px;
    height: 60px;
  }
  .header__icon {
    width: 40px;
    height: 40px;
  }
}
.button {
  position: relative;
  box-sizing: border-box;
  display: block;
  margin: 0;
  padding: 0;
  font-family: inherit;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  transition: 0.3s;
}

.settings {
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  padding: 20px 10px;
  color: #FFFFFF;
  outline: none;
  transition: all 0.8s, background-color 0.4s;
  z-index: 3;
}
.settings--hide {
  opacity: 0;
  pointer-events: none;
  transform: translateY(-100%);
}

.settings__close {
  position: absolute;
  right: 15px;
  bottom: 26px;
  width: 45px;
  height: 45px;
  z-index: 2;
}

.settings__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 30px;
  transform: translate(-50%, -50%);
  fill: #FFFFFF;
}

.settings__wrapper {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: calc(100vh - 183px - 60px);
}

.settings__control {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.settings__control--hide {
  display: none;
}

.settings__button {
  margin-right: 15px;
  margin-bottom: 7px;
  padding: 10px 15px;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 700;
  border-radius: 30px;
  transition: all 0.6s, font-size 0s;
}
.settings__button:last-child {
  margin-right: 0;
}
.settings__button--active {
  box-shadow: 0 0 10px 3px rgba(255, 255, 255, 0.9);
}

.settings__tab {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  outline: none;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.2s;
}
.settings__tab--active {
  transform: translate(-50%, -50%) scale(1);
}
.settings__tab--scroll {
  top: 50px;
  bottom: 50px;
  overflow-y: scroll;
  transform: translate(-50%, 0%) scale(1);
}

.settings__label {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 6px;
  cursor: pointer;
  transition: 0.2s;
}
.settings__label:last-child {
  margin-bottom: 0;
}
.settings__label--hide {
  position: relative;
  margin-top: -28px;
  opacity: 0;
  transform: scale(0);
}

.settings__checkbox {
  width: 15px;
  height: 15px;
  margin: 0;
  margin-right: 10px;
}
.settings__checkbox:disabled {
  cursor: not-allowed;
}

.settings__name {
  font-size: 16px;
  font-weight: 300;
}

.settings__sub {
  margin: 0;
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.settings__time {
  display: block;
  margin-bottom: 10px;
  font-size: 13px;
  font-weight: 700;
}

.settings__text {
  margin-top: 0;
  margin-bottom: 25px;
  padding: 0 15px;
  padding-right: 25px;
  font-weight: 300;
}
.settings__text:last-child {
  margin-bottom: 0;
}

.settings__list {
  margin: 0;
  padding: 0 15px;
  text-align: center;
}

.settings__item {
  margin-bottom: 15px;
  font-weight: 300;
  list-style: inside;
}
.settings__item:last-child {
  margin-bottom: 0;
}

.settings__video {
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 15px;
  padding: 12px 15px;
  color: #FFFFFF;
  font-size: 15px;
  font-weight: 300;
  border: 1px solid #FFFFFF;
  border-radius: 30px;
}
.settings__video:last-child {
  margin-bottom: 0;
}
.settings__video--file {
  border: 1px solid #FCF811;
}

.settings__file {
  display: none;
}
.settings__file::-webkit-file-upload-button {
  display: none;
}

.settings__output {
  display: block;
  max-width: 150px;
  text-overflow: ellipsis;
  text-wrap: nowrap;
  overflow: hidden;
}

.settings__counter {
  position: absolute;
  top: 5px;
  right: -45px;
  box-sizing: border-box;
  min-width: 34px;
  min-height: 30px;
  padding: 9px 6px;
  text-align: center;
  font-size: 12px;
  font-weight: 400;
  border-radius: 50%;
  transition: 0.2s;
}
.settings__counter--hide {
  opacity: 0;
  transform: scale(0);
}

@media (min-width: 768px) {
  .settings {
    overflow-y: unset;
  }
  .settings__close {
    top: 25px;
    right: 25px;
    left: auto;
    width: 45px;
    height: 45px;
  }
  .settings__icon {
    width: 33px;
    height: 33px;
  }
  .settings__control {
    padding-right: 75px;
  }
  .settings__button {
    margin-bottom: 15px;
    font-size: 18px;
  }
  .settings__tab {
    max-width: 700px;
  }
  .settings__tab--scroll {
    top: 120px;
    bottom: 120px;
  }
  .settings__label {
    justify-content: center;
  }
  .settings__label--hide {
    margin-top: -30px;
  }
  .settings__name {
    font-size: 18px;
  }
  .settings__sub {
    font-size: 30px;
  }
  .settings__text {
    font-size: 18px;
  }
  .settings__item {
    font-size: 18px;
  }
  .settings__video {
    padding: 15px 25px;
    font-size: 18px;
  }
  .settings__file {
    max-width: 185px;
    padding: 15px 20px;
    font-size: 18px;
  }
  .settings__output {
    max-width: 200px;
  }
  .settings__counter {
    top: 10px;
  }
}
@media (min-width: 1440px) {
  .settings {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .settings__close {
    top: 35px;
    right: 35px;
    width: 60px;
    height: 60px;
  }
  .settings__icon {
    width: 40px;
    height: 40px;
  }
  .settings__control {
    width: 40%;
    margin-bottom: 0;
    padding-right: 0;
  }
  .settings__button {
    padding: 15px 25px;
    font-size: 25px;
  }
  .settings__wrapper {
    width: 60%;
  }
  .settings__wrapper::-webkit-scrollbar {
    width: 3px;
    height: 3px;
  }
  .settings__tab {
    max-width: none;
  }
  .settings__tab--scroll {
    top: 50px;
    bottom: 50px;
  }
  .settings__label {
    margin-bottom: 10px;
  }
  .settings__label--hide {
    margin-top: -44px;
  }
  .settings__name {
    font-size: 25px;
  }
  .settings__sub {
    margin-bottom: 30px;
    font-size: 40px;
  }
  .settings__text {
    margin-bottom: 30px;
    font-size: 23px;
  }
  .settings__time {
    font-size: 20px;
  }
  .settings__item {
    font-size: 25px;
  }
  .settings__video {
    font-size: 22px;
  }
  .settings__file {
    max-width: 240px;
    font-size: 25px;
  }
  .settings__counter {
    top: 12px;
    right: -50px;
    font-size: 14px;
  }
}
@media (min-width: 1440px) {
  .main {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 15px;
  }
  .main--cinema {
    margin: 0;
  }
}
.background {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  transition: 0.6s;
}
.background--off {
  opacity: 0;
}

.background__video {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.background__video[src] ~ .background__overlay {
  background-color: rgba(0, 0, 0, 0.8);
}

.background__overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  transition: 0.6s;
}

.video {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  outline: none;
  transition: 0.6s;
}
.video::-webkit-media-controls {
  display: none;
}
.video::-webkit-media-enclosure {
  display: none;
}
.video::-webkit-media-panel {
  display: none;
}
.video::-moz-media-controls {
  display: none;
}
.video[src] {
  background-color: #000000;
}

::cue {
  font-size: 12px;
  font-weight: 500;
  font-family: "Chalet", "Nunito", "Roboto", "Arial", sans-serif;
  text-align: center;
  text-shadow: 0px 0px 5px #000000, 0px 0px 5px #000000;
  background-color: transparent;
}

.video__wrapper {
  position: relative;
  box-sizing: border-box;
  width: 95%;
  max-width: 600px;
  margin: 0px auto;
  border-radius: 10px;
  overflow: hidden;
  transition: 0.3s;
  transform-style: preserve-3d;
  animation-duration: 0.8s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}
.video__wrapper:fullscreen {
  border-radius: 0px;
}
.video__wrapper:fullscreen .video__start {
  border-radius: 0px;
}
.video__wrapper:fullscreen .video {
  border-radius: 0;
  object-fit: contain;
  background-color: #000000;
}
.video__wrapper:fullscreen .control {
  border-radius: 0;
}
.video__wrapper:fullscreen .control__button--pip {
  display: none;
}
.video__wrapper:fullscreen .control__button--fit {
  display: block;
}
.video__wrapper:fullscreen .control__button--cinema {
  display: none;
}
.video__wrapper:fullscreen .statistics {
  border-top-right-radius: 0;
}
.video__wrapper--subtitle ::cue {
  background-color: rgba(0, 0, 0, 0.4);
}
.video__wrapper--waiting {
  animation-name: load;
}
.video__wrapper--error {
  box-shadow: 0 0 15px 5px rgba(243, 80, 80, 0.8);
}
.video__wrapper--pause {
  animation-name: pause;
}
.video__wrapper--cinema {
  width: 100%;
  height: 100vh;
  border-radius: 0;
  background-color: #000000;
}

@keyframes load {
  100% {
    box-shadow: 0 0 15px 5px #5be462;
  }
}
@keyframes pause {
  100% {
    box-shadow: 0 0 15px 5px #6320EE;
  }
}
.video__start {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline: none;
  border-radius: 10px;
  transition: background-color 0.8s, opacity 0.8s, transform 0s;
  transition-delay: 0s, 0s, 0.3s;
  z-index: 5;
}
.video__start--hide {
  background-color: transparent;
  opacity: 0;
  transform: scale(0);
}
.video__start::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.video__start:hover .video__icon, .video__start:focus .video__icon {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1.2);
}
.video__start:hover .video__icon path, .video__start:focus .video__icon path {
  opacity: 1;
}
.video__start:active .video__icon {
  transform: translate(-50%, -50%) scale(0.9);
}

.video__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0.8;
  transform: translate(-50%, -50%);
  transition: 0.2s;
}

@media (min-width: 768px) {
  ::cue {
    font-size: 18px;
  }
  .video__wrapper {
    max-width: none;
  }
  .video__icon {
    width: 150px;
    height: 150px;
  }
}
@media (min-width: 1024px) {
  ::cue {
    font-size: 20px;
  }
  .video__wrapper {
    max-width: 900px;
  }
  .video__wrapper--cinema {
    max-width: none;
  }
}
@media (min-width: 1440px) {
  ::cue {
    font-size: 28px;
  }
  .video__wrapper {
    width: 1200px;
    max-width: none;
    margin: 0;
  }
  .video__wrapper:fullscreen ::cue {
    font-size: 34px;
  }
  .video__wrapper--cinema {
    width: 100%;
  }
  .video__icon {
    width: 250px;
    height: 250px;
  }
}
.statistics {
  position: absolute;
  top: 0px;
  right: 0px;
  box-sizing: border-box;
  padding: 12px 16px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: rgba(33, 26, 29, 0.8);
  transition: 0.7s;
}
.statistics--hide {
  transform: translateY(-101%);
}
.statistics--off {
  display: none;
}

.statistics__category {
  margin: 0px;
  font-size: 10px;
}
.statistics__category:last-child {
  margin-bottom: 0;
}
.statistics__category span {
  color: #FFFFFF;
  font-weight: 300;
}
.statistics__category--hide {
  display: none;
}

.statistics__name {
  position: absolute;
  top: 0px;
  left: 0px;
  box-sizing: border-box;
  width: 100%;
  margin: 0px;
  padding: 15px 18px;
  font-size: 12px;
  font-weight: 400;
  text-shadow: 0px 0px 5px #000000, 0px 0px 5px #000000;
  text-overflow: ellipsis;
  text-wrap: nowrap;
  transition: 0.7s;
  pointer-events: none;
  overflow: hidden;
}
.statistics__name--hide {
  transform: translateY(-101%);
}
.statistics__name--off {
  display: none;
}
.statistics__name--short {
  padding-right: 140px;
}

@media (min-width: 768px) {
  .statistics {
    padding: 15px 22px;
  }
  .statistics__category {
    margin-bottom: 3px;
    font-size: 13px;
  }
  .statistics__name {
    padding: 30px 40px;
    font-size: 16px;
  }
  .statistics__name--short {
    padding-right: 175px;
  }
}
@media (min-width: 1440px) {
  .statistics {
    padding: 20px 30px;
  }
  .statistics__category {
    margin-bottom: 5px;
    font-size: 18px;
  }
  .statistics__name {
    font-size: 22px;
  }
  .statistics__name--short {
    padding-right: 240px;
  }
}
.control {
  position: absolute;
  bottom: 0%;
  left: 0px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  padding: 15px 10px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: rgba(33, 26, 29, 0.8);
  transition: 0.8s;
}
.control--hide {
  pointer-events: none;
  transform: translateY(101%);
}
.control--off {
  display: none;
}
.control:focus-within {
  transform: translateY(0);
}

.control__line {
  position: absolute;
  top: -5px;
  right: 5px;
  left: 5px;
  width: 0%;
  height: 2px;
  border-radius: 5px;
  opacity: 0.7;
  transition: 0.6s;
}
.control__line--hide {
  display: none;
}
.control__line::-webkit-progress-bar {
  border-radius: 5px;
}
.control__line::-webkit-progress-value {
  border-radius: 5px;
}

.control__button {
  width: 35px;
  height: 35px;
  margin-right: 15px;
  margin-bottom: 5px;
  transition: 0.2s;
}
.control__button:last-child {
  margin-right: 0;
}
.control__button--hide {
  display: none;
}
.control__button--off {
  display: none;
}
.control__button--prev {
  transform: rotate(180deg);
}

.control__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.2s;
}
.control__icon--hide {
  display: none;
}
.control__icon--unmuted {
  opacity: 0;
}
.control__icon--muted {
  opacity: 1;
}

@keyframes muted {
  from {
    transform: translate(-50%, -50%) scale(0);
  }
  to {
    transform: translate(-50%, -50%) scale(1);
  }
}
.control__info {
  position: absolute;
  top: -5px;
  right: -5px;
  padding: 0 4px;
  color: #FFFFFF;
  font-size: 10px;
  border-radius: 10px;
  transition: 0.1s;
  z-index: 1;
}
.control__info--hide {
  transform: scale(0);
}

.control__box {
  display: flex;
  align-items: center;
}
.control__box:hover .control__button--mute, .control__box:focus .control__button--mute, .control__box:focus-within .control__button--mute {
  margin-right: 5px;
}
.control__box:hover .control__range--volume, .control__box:focus .control__range--volume, .control__box:focus-within .control__range--volume {
  width: 60px;
  padding: 10px;
  clip-path: none;
}

.control__muted {
  transition: 0.1s;
}
.control__muted--hide {
  transform: scale(0);
}

.control__duration {
  position: relative;
  display: flex;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  margin: 0;
  margin-bottom: 10px;
}

.control__time {
  box-sizing: border-box;
  min-width: 60px;
  padding: 0px 5px;
  font-size: 11px;
  font-weight: 300;
  text-align: center;
}
.control__time:last-child {
